import React, { Component } from 'react';
import { Document, Page, pdfjs } from "react-pdf";
import resumeFile from "./myResume.pdf"
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;


export default class About extends Component {
    constructor(props){
      super(props);
      this.state = { viewResume: false};
    }

    click = ()=>{
      this.setState({
        viewResume: true
      })
    }
    render() {
        let ResumeData = this.props.resumeData;
        return (
    
          /* About Section
       ================================================== */
       <React.Fragment>
          <section id="about">
            <div className="row">
              <div className="three columns">
                <img className="profile-pic" src="images/profilePic.jpg" alt="" />
              </div>
              <div className="nine columns main-col">
                <h2>About Me</h2>
                <p>{ResumeData.aboutMe}
                </p>
                <div className="row">
                  <div className="columns contact-details">
                    <h2>Contact Details</h2>
                    <p className="address">
                      <span>Ryan Tendonge</span><br />
                      <span>374 Boston Street<br />
                        Lynn, MA 01905 US
                      </span><br />
                      <span>(857)417-0778</span><br />
                      <span>ryantenas@gmail.com</span>
                    </p>
                  </div>
                  <div className="columns download">
                  <a href="#" className="button" onClick={(e)=>{this.setState({viewResume: true}); e.preventDefault();}}><i className="fa fa-download" />View Resume</a>
                   {this.state.viewResume ? ( 
                   <p>
                      <Document 
                      file={resumeFile}
                      >   
                      <Page pageNumber={1} />
                      </Document>
                   </p>) : ""}
                  </div>
                </div> {/* end row */}
              </div> {/* end .main-col */}
            </div>
          </section> 
          </React.Fragment>
        );
    }
}